@import "../abstracts/variables";

@keyframes moveInLeft{
    0%{
        opacity: 0;
        transform: translateX(-10rem)
    }
    80%{
        transform: translateX(1rem)
    }
    100%{
        opacity: 100;
        transform: translate(1)
    }
}
@keyframes moveInRight{
    0%{
        opacity: 0;
        transform: translateX(10rem)
    }
    80%{
        transform: translateX(-1rem);
    }
    100%{
        opacity: 100;
        transform: translate(1)
    }


}
@keyframes moveUp{
    0%{
        opacity: 0;
        transform: translateY(5rem)
    }
    100%{
        opacity: 100;
        /* transform: translate(1) */
    }
}
