.header{
    background-image:   linear-gradient(to right bottom,
                        rgba($color-primary-light, 0.8), 
                        rgba($color-primary, 0.8)) , 
                        url("../img/hero.jpg");
    height: 95vh;
    background-size: cover;
    background-position: top;
    clip-path: polygon(0 0, 0 100%, 100% 75vh, 100% 0);
    position: relative;
    
    &__logo-box {
        position: absolute;
        top: 4rem;
        left: 4rem;
      }
      
    &__logo {
        height: 3.5rem;
      }
}